/*
0 - 600:      Phone
601 - 960:    Tablet Potrait
961 - 1280:   Tablet Landscape
1281 - 1440:  Desktop

1800 >        Big Desktop
*/
* {
  padding: 0;
  margin: 0;
  outline: none;
  box-sizing: border-box;
}

img {
  max-width: 100%;
}

body {
  background: red;
}

@media screen and (min-width: 600px) {
  body {
    background: purple;
  }
}

@media screen and (min-width: 960px) {
  body {
    background: green;
  }
}

@media screen and (min-width: 1280px) {
  body {
    background: magenta;
    color: white;
  }
}

@media screen and (min-width: 1440px) {
  body {
    background: turquoise;
  }
}

.logo {
  width: 100%;
  height: 100px;
  cursor: pointer;
  padding: 0.5rem 0 0 0.5rem;
}

.logo img {
  width: 50%;
}

@media screen and (min-width: 960px) {
  .logo img {
    width: 100%;
  }
}

#header {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 50px auto;
  grid-gap: 50px;
  margin-bottom: 30px;
}

@media screen and (min-width: 960px) {
  #header {
    grid-template-columns: 1fr 11fr;
  }
}

#header ul {
  display: grid;
  list-style-type: none;
  width: 90%;
}

@media screen and (min-width: 960px) {
  #header ul {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
  }
}

@media screen and (min-width: 960px) {
  #header ul li {
    margin: 0 1rem;
  }
}

#header ul li a {
  display: block;
  text-decoration: none;
  padding: 0.5rem 1.2rem;
  background: white;
  color: black;
  text-transform: uppercase;
  width: 100%;
  transition: all 0.5s ease-in-out;
}

@media screen and (min-width: 960px) {
  #header ul li a {
    background: none;
    display: inline-block;
    padding: 0px 0px;
    width: auto;
  }
}

#header ul li a:hover, #header ul li a:active {
  background: rgba(255, 255, 255, 0.5);
  color: white;
}

@media screen and (min-width: 960px) {
  #header ul li a:hover, #header ul li a:active {
    background: none;
    border-bottom: 1px solid white;
  }
}

#header ul li:not(:last-of-type) {
  border-bottom: 1px solid #f5f5f5;
}

@media screen and (min-width: 960px) {
  #header ul li:not(:last-of-type) {
    border-bottom: none;
  }
}

.mobileMenuButton {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  flex-direction: column;
  padding-right: 4%;
  cursor: pointer;
}

@media screen and (min-width: 960px) {
  .mobileMenuButton {
    display: none;
  }
}

.mobileMenuButton > div {
  width: 23px;
  height: 7px;
  border-bottom: 2px solid white;
}

.sidenav {
  background: #29abe2;
  padding-top: 50px;
  padding-bottom: 20px;
  box-shadow: 0px 0px 8px rgba(127, 127, 127, 0.26), 0px 0px 8px rgba(127, 127, 127, 0.26);
}

.sidenav ul {
  list-style-type: none;
}

.sidenav ul li {
  height: 25px;
  line-height: 25px;
  padding-left: 15px;
}

.sidenav ul li a {
  text-decoration: none;
  color: white;
  text-transform: uppercase;
  font-size: 0.8rem;
}

.sidenav .btns {
  padding-top: 30px;
  width: 90%;
  margin: 0 auto;
}

.sidenav .btns button {
  padding: 0.5rem 1rem;
  background: transparent;
  border: none;
}

.sidenav .btns button:nth-of-type(1) {
  margin-bottom: 10px;
  background: #33358f;
  color: white;
  border-radius: 7px;
  border: 2px solid white;
}

.sidenav .btns button:nth-of-type(2) {
  margin-bottom: 10px;
  background: white;
  color: #33358f;
  border-radius: 7px;
  border: 2px solid #33358f;
}

main {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 2%;
  width: 100%;
  max-width: 960px;
  height: 100vh;
  margin: 0 auto;
  padding-left: 1.74rem;
  padding-right: 1.74rem;
}

@media screen and (min-width: 960px) {
  main {
    grid-template-columns: 3fr 9fr;
  }
}

footer {
  width: 80%;
  margin: 50px auto 0 auto;
}
